export default `
<script>
{
    let hege = ["Cecilie", "Lone"];
    let stale = ["Emil", "Tobias", "Linus"];
    let value = hege.concat(stale);
    write(value);
}
{
    let hege = ["Cecilie", "Lone"];
    let stale = ["Emil", "Tobias", "Linus"];
    let value = hege.concat(stale, stale, '123', 123);
    write(value);
}
</script>
`



